	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapter 14 - The inhibitor","texte":{"childMarkdownRemark":{"html":"<p>Daryl, Éléa and William had arrived around 3 in the morning. William had fallen asleep in the car and Éléa and Daryl had discussed what had happened on his side.</p>\n<p>Darakei approached when Éléa had finished parking in their entrance. Éléa got out of the car, followed by William rubbing his eyes, he was still asleep.</p>\n<p>Darakei: Go to your room.<br>\nWilliam: Yes dad...</p>\n<p>Wiliam comes into the house and goes to her room. Daryl gets out of the car and closes the door. He turns to Darakei.</p>\n<p>Darakei: You want a coffee or something?<br>\nDaryl: I could use a cup of coffee, thank you.<br>\nÉléa : I'll make some.</p>\n<p>We enter the house and go into the kitchen. Éléa starts making coffees.</p>\n<p>Darakei: First, where are they?<br>\nDaryl: They're in a disused factory south of Giotry.<br>\nDarakei: Would you be able to point me to the location on a map?<br>\nDaryl: Yes, but I doubt they're still here.<br>\nDarakei: I'll still send men just in case. At least if we can find them.<br>\nDaryl: That's right.</p>\n<p>Darakei hands him his cell phone. Daryl will look at the map and indicate the exact location of the warehouse. Éléa puts two coffees next to the men and starts drinking her own.</p>\n<p>Éléa : And what are we going to do about William?<br>\nDarakei : I let you take care of it, between a teenager who has had his little crisis and a dozen New Human criminals, the choice is not difficult.<br>\nÉléa : Wow, a whole family man.</p>\n<p>Éléa leaves the room angry. Darakei sighs while Daryl hands him the card.</p>\n<p>Darakei: Thank you Daryl.</p>\n<p>Darakei takes his phone and sends the coordinates to his troops who are already in Giotry. Daryl's watching Darakei.</p>\n<p>Darakei: It's okay, don't stare at me like that.<br>\nDaryl: Sorry.<br>\nDarakei: Do you remember any of SIAM's technologies?<br>\nDaryl: Yes, mostly the chemical compound of the inhibitor.<br>\nDarakei: Serious? Would you be able to reproduce the inhibitor?<br>\nDaryl: I only know the recipe, but I don't know how to manage the instruments.<br>\nDarakei: Give me the recipe and I'll take care of the rest.<br>\nDaryl: If you have paper and pencil.<br>\nDarakei: I'll get that for you.</p>\n<p>Darakei gets up and goes running for what it takes. Daryl waits and drinks a sip of coffee while waiting. Darakei comes back a moment later with papers and a pencil.</p>\n<p>Darakei: There.<br>\nDaryl: Thank you.</p>\n<p>Daryl takes the paper and pencil and starts drawing a fairly complex chemical compound. Darakei watches him draw, stunned.</p>\n<p>Darakei: Of course!<br>\nDaryl: From?<br>\nDarakei: Keep drawing, finish the compound, I know how to handle it.<br>\nDaryl: You're seeing Darakei.</p>\n<p>Daryl finishes the drawing and adds the names of the components around the drawing.</p>\n<p>Darakei: Thanks, Daryl, you don't know how much this is gonna help us.<br>\nDaryl: It was my task to memorize this component, three quarters of the employees had to know it by heart in case of a problem.<br>\nDarakei : I understand better, while I think about it, I would need a sample of your blood for tests...<br>\nDaryl: Later, I'm exhausted.<br>\nDarakei: Okay, I'll take you to your room.<br>\nDaryl: Thank you Darakei.</p>\n<hr>\n<p>Flynn arrives at Daryl's room door and knocks wildly.</p>\n<p>Flynn: Hey Daryl! We need your help lifting something!</p>\n<p>He waits a moment in front of the door and knocks again.</p>\n<p>Flynn: Daryl! Yo!</p>\n<p>He tries to open the door and notices it's locked.</p>\n<p>Flynn: Fuck.</p>\n<p>Flynn moves his hand in front of the door and raises the latches via his power before opening the door. He enters the room and sees that the window is open.</p>\n<p>Flynn: Fuck! He's gone! He's gone!</p>\n<p>He's off to the race to join the others.</p>\n<p>Flynn: Guys! Daryl ran out his bedroom window!</p>\n<p>The alert was quickly sounded and nearly three quarters of the group left, looking for a lead that could tell where Daryl had gone. During this time, the rest of the team prepares the evacuation of the place and gathers all that is important. The searches being unsuccessful, the group meets and leaves towards a second site.</p>\n<p>Yuu approaches his older brother, looking worried.</p>\n<p>Yuu : What do we do if they find us anyway?<br>\nFlynn: Daryl has no way of knowing where we're going because he heard is a fake address.<br>\nYuu : Oh! You're brilliant brother!<br>\nFlynn: Thanks little sister.</p>\n<p>Saeko passed close to them and growls a little.</p>\n<p>Saeko: It was my idea, little head.<br>\nYuu : Oh! It's not right to lie~<br>\nFlynn: Come on, everybody! Let's finish cleaning up and get the hell out of here! Kellan, you know what to do?<br>\nKellan: Yep!</p>\n<p>Kellan hits his fists against each other with a smile. The Black Eagles and The Legion quickly moved away from their former hiding place. Only Kellan remains in the building and begins to destroy the support beams. The building soon collapsed. Kellan joins them a few seconds later by removing the dust that had fallen on him.</p>\n<p>Kellan: A good thing done!<br>\nFlynn: Come on! We're leaving! Everybody in the vehicles!</p>\n<p>Drake looks at Saeko, their look means something, but what. All get into their vehicles and head for their base further northeast. The group arrived at their new quarters around six in the morning. They were all exhausted and went to sleep.</p>\n<p>On the GenCorp side, they arrive at the warehouse that Daryl had indicated. One of the guards contacts Darakei to warn him of the state of play.</p>\n<p>Darakei: Yes?<br>\nPrivate: Good morning, sir, we just arrived at the scene Daryl had indicated, the warehouse collapsed. We'll conduct a search and search for clues.<br>\nDarakei: Okay, proceed.</p>\n<p>Darakei sighing and hanging up, he wanted to sleep. He decided to get up, get dressed and go to the office, without forgetting to kiss Elea's forehead, who was still sleeping.</p>"}},"chapitreAvant":"chapter-13-escape-for-freedom","chapitreApres":"chapter-15-out-of-control","nomRoman":"the-first-cyborg-volume-2","slug":"chapter-14-the-inhibitor","codeChapitre":"H0002C014","equivalentUrl":"/histoires/chapitre/chapitre-14-l-inhibiteur"}},"pathContext":{"slug":"chapter-14-the-inhibitor"}}

